a > *, .gov-link > * {
  color: inherit;
}

.gov-list--plain li {
  margin-bottom: 0;
}

.scrollbar-fix .body-fixed {
  padding-right: 15px;
}
.scrollbar-fix .body-fixed #main-header {
  padding-right: 15px;
}

::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border: 2px solid transparent;
  background-clip: content-box;
}

@layer components {
  .top-header__extra {
    @apply flex gap-6;
  }
  .main-header__image-nav {
    @apply flex items-center gap-10 flex-1 justify-between xl:flex-none;
  }
  .main-header__extra {
    @apply flex gap-6;
  }
  /*  RESPONSE
    ------------------------------------------------------------------ */
}
@layer components {
  .navigation {
    @apply relative;
    /*  DESKTOP layout
    ------------------------------------------------------------------ */
  }
  .navigation__menu > .navigation__item {
    @apply mb-0;
  }
  .navigation__menu--mobile-top-nav {
    @apply flex flex-col gap-3;
  }
  .navigation__item {
    position: initial;
  }
  .navigation__item::before {
    content: none;
  }
  .navigation__link {
    @apply relative block hover:text-primary focus:outline-0;
  }
  .navigation__link.is-active {
    @apply text-secondary bg-secondary-100 border-b-3 border-primary;
  }
  .navigation__mobile {
    @apply hidden;
  }
  @media screen and (min-width: 1280px) {
    .navigation__menu {
      @apply flex flex-row items-center gap-8 h-max;
    }
    .navigation__link {
      @apply h-max;
    }
  }
  @media screen and (max-width: 1280px) {
    .navigation:not(.navigation__mobile) {
      @apply hidden;
    }
  }
}
@layer components {
  .hamburger-nav--close {
    @apply hidden relative;
  }
  .hamburger-nav--modal.is-open {
    @apply block fixed inset-0 bg-neutral-white;
  }
  .hamburger-nav--modal.is-open > .modal-content {
    @apply block mx-4 h-full overflow-scroll;
  }
  .hamburger-nav__mobile-nav, .hamburger-nav__mobile-top-nav {
    display: none;
  }
  .hamburger-nav__is-open {
    @apply overflow-hidden;
  }
  .hamburger-nav__is-open .hamburger-nav {
    @apply hidden;
  }
  .hamburger-nav__is-open .hamburger-nav--close {
    @apply block;
  }
}
@layer components {
  .tile-mobile {
    @apply flex items-center gap-2 p-2 hover:bg-primary-200;
  }
  .tile-mobile gov-icon {
    width: calc(var(--gov-text-m-font-size) * 1.2);
    height: calc(var(--gov-text-m-font-size) * 1.2);
  }
  .tile-mobile__label {
    @apply block bg-secondary-300 text-secondary-700 pl-2;
  }
  .tile-mobile__content-links {
    @apply pl-2;
  }
  .tile-mobile::after {
    content: "";
    background: url("../icons/basic/chevron-right.svg") no-repeat;
    filter: saturate(0%) brightness(0%) invert(38.6274509804%) opacity(1);
    filter: url('data:image/svg+xml;utf8,    <svg xmlns="http://www.w3.org/2000/svg">      <filter id="recolor" color-interpolation-filters="sRGB">        <feColorMatrix type="matrix" values="          0 0 0 0 0.137254902          0 0 0 0 0.3843137255          0 0 0 0 0.6352941176          0 0 0 1 0        "/>      </filter>    </svg>    #recolor');
    @apply block ml-auto bg-contain h-4 w-4;
  }
}